import { Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material'
import React from 'react'
import Paper from '@mui/material/Paper';
import ChangeHistoryIcon from '@mui/icons-material/ReportProblem';
function Details({ width ,data}) {
    return (
        <div className="flexa2" style={{ width: width }}>
            <div className='alarmes '>
                <div className="titre red" style={{animation:data.AQ > 0 ? "titreanim 2s ease 0s infinite normal forwards" : ""}}>
                    Qualit
                    {data.AQ > 0 && (<div>
                    <ChangeHistoryIcon />
                    </div>)}
                </div>
                <div className="data">
                    <div className="dataTitre flexa noW">
                        <div>
                            Temps
                        </div>
                        <div>
                            Freq
                        </div>
                        <div>
                            Alarme
                        </div>
                    </div>
                    <div className='dmt flexa'>
                        <div>
                            {data.TQ} m
                        </div>
                        <div>
                        {data.FQ}
                        </div>
                        <div>
                        {data.AQ}
                        </div>
                    </div>
                </div>
            </div>
            {data.name != "MONTAGE" && (
            <div className='alarmes '>
                <div className="titre blue2" style={{animation:data.AP> 0 ? "titreanim 2s ease 0s infinite normal forwards" : ""}}>
                Prftechnik
                {data.AP > 0 && (<div>
                    <ChangeHistoryIcon />
                    </div>)}
                </div>
                <div className="data">
                    <div className="dataTitre flexa">
                        <div>
                            Temps
                        </div>
                        <div>
                            Freq
                        </div>
                        <div>
                            Alarme
                        </div>
                    </div>
                    <div className='dmt flexa'>
                        <div>
                        {data.TP} m
                        </div>
                        <div>
                        {data.FP}
                        </div>
                        <div>
                    {/* <ChangeHistoryIcon /> */}
                        {data.AP}
                        </div>
                    </div>
                </div>
            </div>


            )
                
            
            }
            <div className='alarmes'>
                <div className="titre yellow" style={{animation:data.AL > 0 ? "titreanim 2s ease 0s infinite normal forwards" : ""}}>
                Logistique
                {data.AL > 0 && (<div>
                    <ChangeHistoryIcon />
                    </div>)}
                </div>
                <div className="data">
                    <div className="dataTitre flexa">
                        <div>
                            Temps
                        </div>
                        <div>
                            Freq
                        </div>
                        <div>
                            Alarme
                        </div>
                    </div>
                    <div className='dmt flexa'>
                        <div>
                        {data.TL} m
                        </div>
                        <div>
                        {data.FL}
                        </div>
                        <div>
                        {data.AL}
                        </div>
                    </div>
                </div>
            </div>
            <div className='alarmes'>
                <div className="titre white"  style={{animation:data.AM > 0 ? "titreanim 2s ease 0s infinite normal forwards" : ""}}>
                Maintenance
                {data.AM > 0 && (<div>
                    <ChangeHistoryIcon />
                    </div>)}
                </div>
                <div className="data">
                    <div className="dataTitre flexa">
                        <div>
                            Temps
                        </div>
                        <div>
                            Freq
                        </div>
                        <div>
                            Alarme
                        </div>
                    </div>
                    <div className='dmt flexa'>
                        <div>
                        {data.TM} m
                        </div>
                        <div>
                        {data.FM}
                        </div>
                        <div>
                        {data.AM}
                        </div>
                    </div>
                </div>
            </div>
            {
                data.name == "MONTAGE" && (
                    <div className='alarmes'>
                        <div className="titre blue3"  style={{animation:data.APU > 0 ? "titreanim 2s ease 0s infinite normal forwards" : ""}}>
                        Production
                        {data.APU > 0 && (<div>
                            <ChangeHistoryIcon />
                            </div>)}
                        </div>
                        <div className="data">
                            <div className="dataTitre flexa">
                                <div>
                                    Temps
                                </div>
                                <div>
                                    Freq
                                </div>
                                <div>
                                    Alarme
                                </div>
                            </div>
                            <div className='dmt flexa'>
                                <div>
                                {data.TPU} m
                                </div>
                                <div>
                                {data.FPU}
                                </div>
                                <div>
                                {data.APU}
                                </div>
                            </div>
                        </div>
                    </div>
                )
            }


        </div>

    )
}

export default Details