import * as React from 'react';
import {Box, Button, Fab, Grid, Modal, TextField, Typography } from '@mui/material';
import { Container } from '@mui/system';
import Chaine from '../components/Chaine';
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { setLoading, setNotification } from '../store/userSlice';
import { userRequest } from '../requestMethods';
import { updateChains } from '../store/andonSlice';
import SettingsIcon from '@mui/icons-material/Settings';


const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};
function dynamicSort(property) {
  var sortOrder = 1;
  if(property[0] === "-") {
      sortOrder = -1;
      property = property.substr(1);
  }
  return function (a,b) {
      var result = (a[property] < b[property]) ? -1 : (a[property] > b[property]) ? 1 : 0;
      return result * sortOrder;
  }
}
export default function Home() {
  

const [open, setOpen] = React.useState(false);
const handleOpen = () => setOpen(true);
const handleClose = () => setOpen(false);
  //const andon = useSelector((state) => state.andon.chains);
  const [andon, setandon] = React.useState([])
  const [settings, setSettings] = React.useState({time:5000})
  // console.log(andon);
  const dispatch = useDispatch();

  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    setSettings({...settings,time:parseInt(data.get("time"))})
    setOpen(false)
    // console.log({
    //   server: data.get('server'),
    //   port: data.get('port'),
    //   topic: data.get('topic'),
    // });
    // dispatch(setsettings({
    //     server: data.get('server'),
    //     port: data.get('port'),
    //     topic: data.get('topic'),
    //     connected:true
    //   }))

    //   document.location.reload();
    // // setSettings({
    // //   server: data.get('server'),
    // //   port: data.get('port'),
    // //   topic: data.get('topic'),
    // // })
  }
  const getData = async (uri) => {
    //dispatch(setLoading(true));
    try{
       
        const { data } = await userRequest.get(uri);
        var d = data.sort(dynamicSort("name"));
        d = data.map(di => JSON.parse(di.data));
        // console.log(d);
        // dispatch(updateChains(data));
        setandon(d);
         //dispatch(setLoading(false));

    }catch(e){
        dispatch(setLoading(false));
    }
};

var intervalId = null
React.useEffect(() => {
  clearInterval(intervalId);
  getData(`/update`);
   intervalId = setInterval(() => {
    getData(`/update`);
  }, settings.time);
  return () => {
    clearInterval(intervalId);
  };
}, [settings]);
  


  return (
    <div className='zoom-con' >

      <Grid container columns={40} spacing={0} className="saftagrid">
        {
          andon.map(a => (
            <Chaine data={a} key={a.name} />
          ))
        }
      </Grid>
      <div  className='fab'>
        <Fab color="primary" aria-label="Settings" onClick={handleOpen}>
        <SettingsIcon />
      </Fab>
      </div>


      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
        <Typography component="h1" variant="h5" className='h11 t11'>
            Settings <SettingsIcon />
          </Typography>
          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              required
              label="Refresh Time"
              name="time"
              defaultValue={settings.time}
              autoFocus
              style={{width: "65%"}}
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
              className="blue"
            >
              Save
            </Button>
          </Box>
        </Box>
      </Modal>

    </div>
  );
}
