import { Grid } from '@mui/material'
import React from 'react'
import Details from './Details'
import Siren from './Siren'

function Chaine({data}) {
    const getColor = (data) => {
      let color = ["#0092a8","#c9c9c9","darkred","gold","darkblue"]
      let al =[data.AP?data.AP:0,data.AM,data.AQ,data.AL,data.APU?data.APU:0]
      const max = Math.max(...al);

      const index = al.indexOf(max);
      // console.log(al);
      if (al[index] <= 0 || index < 0 ){
          // console.log("green");
          return "darkgreen";

        }
        // console.log(color[index]);
        return color[index]
    }
  return (
    <Grid sm={39} lg={19} md={19} xl={13} style={{position:'relative',marginTop:20}}>
        <div className="title2">{data.name}</div>
        {/* {console.log(getColor(data))} */}
        <div className='flexv2'>
            <Siren width={"25%"} color={getColor(data)} name={data.name} level={Math.max(data.AP?data.AP:0,data.AM,data.AQ,data.AL,data.APU?data.APU:0)} />
            <Details width={"74%"} data={data}/>

        </div>
    </Grid >
  )
}

export default Chaine